body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.block{
    display:block;
}
.task{
    cursor: pointer;
    background-color: #555;
    color: #fff;
    left:5px;
    top:5px;
    border-radius: 6px;
    padding: 8px 0;
    margin: 5px;
}

.add_task {
    background-color: #552;
    color: #fff;
    position: absolute;
    left: 35%;
    border-radius: 6px;
    padding: 8px;
    display: flex;
    flex-direction: column;
    width: 50%;
    z-index: 3;
}

.button_group{
    display: flex;
    justify-content: space-around;
    margin: 0.5em;
}

.preview{
    position: absolute;
    width: 80%;
    height: 80%;
    background-color: #555;
    color: #fff;
    left:10%;
    top:10%;
    border-radius: 6px;
    padding: 8px 0;
    z-index: 5;
}

.content {
    margin: 2em;
}
